/* COLORS */
$white: #FFFFFF;
$light-grey: #F6F4F4;
$beige-bright: #A89B94;
$beige-medium: #918178;
$beige-dark: #655953;
$blue-dark: #2F4858;
$green-dark: #2F474B;
$orange-brown: #A66A5E;
$brown: #A28967;
$black: #000000;

// COLORS WITH TRANSPARENCY
$transparent-white: transparentize($color: $white, $amount: 0.40);

* {
	font-family: "Heebo", Helvetica, sans-serif;
	font-weight: 300;
	line-height: 1.5;
}

.heading, h1, h2, h3, h4, h5 {
	font-family: 'Playfair Display', serif;
  font-weight: 400;
  color: $blue-dark;
}

.heading--h1 {
  font-size: 80px;
  font-weight: 600;	
	line-height: 1.2;
}

.heading--h2 {
  font-size: 240px;
  font-weight: 400;
	line-height: 1;
	opacity: 0.08;
	white-space: nowrap;
	text-transform: uppercase;
}

.heading--h3 {
  font-size: 48px;
  font-weight: 400;
	line-height: 1.2;
}

.heading--h4 {
  font-size: 20px;
  font-weight: 500;
	line-height: 1.2;
}

.paragraph, p {
	font-size: 18px;
	font-family: "Heebo", Helvetica, sans-serif;

	&--bold { font-weight: 600; }
	&--small { font-size: 14px; }
	&--big { font-size: 32px; font-family: "Playfair Display"; }

}

.link, a {
	font-family: "Heebo", Helvetica, sans-serif;
	font-size: 18px;
	line-height: 26px;
	line-height: 1.2;
	text-decoration: none;
	font-weight: 400;

	&--small {
		font-size: 14px;
	}

	&--serif {
		font-family: 'Playfair Display', serif;
		font-weight: 400;
		font-size: 32px;
	}

}

.highlight {
	font-weight: inherit;
	font-family: inherit;
}


* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

.body {
	width: 100%;
	max-width: 100vw;
	overflow-x: hidden;

	&--dark { background-color: $green-dark; }
	&--light { background-color:$light-grey;  }

}



.heading {
  color: $blue-dark;
	 
  &--black { color: $black }
  &--beige-dark { color: $beige-dark }
  &--blue-dark { color: $blue-dark }
	&--white { color: $white }

	.heading--h2 {
		
		&--beige { color: $beige-medium }
		&--white { color: $white }
		
	}

}
.hello-world {
	color: white;
	text-shadow: 0px 0px 2px $blue-dark;
}
.highlight {
	color: $black;

  &--black { color: $black; }
  &--beige-dark { color: $beige-dark; }
  &--blue-dark { color: $blue-dark; }
	&--white { color: $white; }

}
 .link {
	 padding: 16px;
	 color: $white;
	 cursor: pointer;
	 display: inline-block;

	 &--black { color: $black; }
	 &--beige-dark { color: $beige-dark; }
	 &--blue-dark { color: $blue-dark; }
	 &--white { color: $white; }

 }
.icon {
  display: inline-block;
  font: normal normal normal 32px 'Icon';
  color: inherit;
  flex-shrink: 0;
	color: $beige-dark;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon--small { font-size: 12px; }
.icon--medium { font-size: 16px; }
.icon--large { font-size: 24px; }
.icon--big { font-size: 32px; }

.icon--angle::before { content: "\ea02"; }
.icon--arrow::before { content: "\ea03"; }
.icon--linkedin::before { content: "\ea04"; }
.icon--close::before { content: "\ea05"; }
.paragraph {

  &--black { color: $black; }
  &--beige-dark { color: $beige-dark; }
  &--beige-medium { color: $beige-medium; }
  &--blue-dark { color: $blue-dark; }
	&--white { color: $white; }

}
.quote {

	&::before { content: "\“"; }
	&::after { content: "\”"; }

}
.text-input {
	width: 100%;
	position: relative;
	border-bottom: 1px solid $transparent-white;
	margin-top: 40px;

	&--error {
		border-bottom: 1px solid $orange-brown;
	}
	
	&__input {
		width: 100%;
		height: 48px;
		color: $white;
		padding: 8px 0;
		outline: none;
		border: none;
		font-weight: 300;
		padding: 0;
		font-size: 18px;
		background-color: transparent;

		&:focus + .text-input__label,
		&:not(:placeholder-shown) + .text-input__label,
		&--active + .text-input__label {
			transform: translate(0, -40px);
			font-size: 12px;
		}

	}
	
	&__label {
		position: absolute;
		color: $transparent-white;
		font-size: 18px;
		font-weight: 200;
		bottom: calc(50% - (24px / 2));
		left: 0px;
		transition: all 0.15s ease;
		pointer-events: none;
	}

}